const coverRootPath = "https://p1.music.126.net/"
const musicRootPath = "http://localhost:8888/api/getMusic?musicName="
                     //https://music.163.com/song/media/outer/url?id=
                     //http://localhost:8888/getMusic?name=

export default {
    state: {
        audio: document.createElement('audio'),
        Music: function({id, name, source, cover, artists, album, duration, favorites}) {
            this.id = id
            this.name = name
            this.source = `${musicRootPath}${source}`
            this.cover = `${coverRootPath}${cover}?param=500y500`
            this.artists = artists
            this.album = album
            this.duration = duration
            this.favorites = favorites
            this.prev = null
            this.next = null
            return this
        },
        musicItem: {
            name: '',
            source: '',
            cover: '',
            prev: null,
            next: null
        },
        musicList: [],
        musicPlayEvent: null,
        playMode: 'list'
    },
    getters: {
        getAudio: state => {
            return state.audio
        },
        getMusicList: state => {
            return state.musicList
        },
        getPrevMusic: state => {
            return state.musicItem.prev
        },
        getNextMusic: state => {
            return state.musicItem.next
        },
        getPlayMode: state => {
            return state.playMode
        },
        getMusicItem: state => {
            return state.musicItem
        }
    },
    mutations: {
        addMusicToList: (state, musicObj) => {
        // if(state.musicList[state.musicList.length - 1])
            state.musicList.push(musicObj)
        },
        setMusicItem: (state, music) => {
            state.musicItem = music
            // state.musicItem.prototype = state.Music
            state.audio.setAttribute('src', state.musicItem.source)
        }
    },
    actions: {
        musicInit: ({commit}, music) => {
            return commit('setMusicItem', music)
        },
        musicListInit: ({state}, musicList) => {
            musicList[0].prev = musicList[musicList.length - 1]
            musicList[0].next = musicList[1]
            for(let i=1; i<musicList.length-1; i++) {
                musicList[i].prev = musicList[i - 1]
                musicList[i].next = musicList[i + 1]
            }
            musicList[musicList.length - 1].prev = musicList[musicList.length - 2]
            musicList[musicList.length - 1].next = musicList[0]

            // commit('setMusicItem', musicList[0])
            return state.musicList = musicList
        },
        playMusic: async ({state, getters}) => {
            if(getters.getPlayListVisibility == true) {
                setTimeout(()=>{
                    state.audio.play()
                },300)
            }else {
                state.audio.play()
            }
        }
        // playNext: ({state}) => {
        //     state
        // }
    }
}