const axios = require('axios')

export const login = ({username,password}) => {
  console.log(username,password)
  return axios.request({
    url: '/api/login',
    params: {
      username,
      password
    },
    method: 'post'
  })
}

export const search = ({keywords}) => {
  return axios.request({
    url: '/api/search',
    params: {
      keywords
    },
    method: 'get'
  })
}